.fielo-table{
  width:100%;
  table-layout: fixed;
  border: none;
  border-collapse:initial;
  .fielo-field {
    white-space: normal;
  }
}

// Responsive design

// Hides the header for mobile
.fielo-table__head {
  display:none;
}

// Each cell to behave as a row
.fielo-table td {
  display: table-row;
}

// shows each header within a row
.fielo-table td:before {
  content: attr(data-th);
  display: table-cell;
}

@media #{$bp-mobile-3} {
  // Gives the table a width
  .fielo-table {
    width: calc(100% - $padding);
    overflow-x: auto;
    display: block;
  }
  // shows the header for mobile
  .fielo-table__head {
    display:table-header-group;
  }
  // Each cell to behave as a cell
  .fielo-table td {
    display: table-cell;
    padding: 0 2px;
  }

  // hides each header within a row
  .fielo-table td:before {
    display: none;
  }
}
