/**
*
* App Menu
* Mobile
*
**/

$inPosition: 0;
$outPosition: -90%;

@mixin appMenuAnimation($in,$out) {
	@keyframes appMenuSlidein {
		from { margin-left: $out; }
		to   { margin-left: $in; }
	}
	@keyframes appMenuSlideout {
	    from { margin-left: $in; }
	    to   { margin-left:$out; }
	}
}

@include appMenuAnimation($inPosition, $outPosition);

// arreglar en el core

.fielo-app-menu {
	&--is-sliding-in .fielo-app-menu__body {
		animation: appMenuSlidein 0.4s forwards;
  }
	&--is-sliding-out .fielo-app-menu__body {
		animation: appMenuSlideout 0.4s forwards;
  }
	&--is-open{
		.fielo-app-menu__btn-bar{
			&:nth-child(1) {
        top: 13px;
				transform: rotate(135deg);
      }
			&:nth-child(2){ opacity: 0; left: -60px;}
			&:nth-child(3){ top: 13px;
				transform: rotate(-135deg);
      }
		}
	}


  .fielo-menu--isHorizontal .fielo-menu__block .fielo-list__item {
    float: none
  }
}

.fielo-app-menu__body {
  top: 0;
  position: absolute;
  margin-left: $outPosition;
  left: 0;
  background: rgba(0, 0, 0, 0.86);
  width: 85%;
  overflow-y: auto;
  z-index:99999;
  padding-top: 40px;
  height: calc(100% - 40px);

  .fielo-link {
    color: #FFF;
  }

  .fielo-menu--isHorizontal .fielo-menu__block .fielo-list__item .fielo-list {
    position: relative;
    top: initial;
    left: initial;
    float: none;
  }
}

.fielo-app-menu__btn {
  position: absolute;
  left: 10px;
  top: 15px;
  width: 16px;
  height: 20px;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
  border-radius: 100%;
  padding: 10px 5px;
  z-index:999999;
}

.fielo-app-menu__btn-bar{
  display: block;
  position: absolute;
  height: 3px;
  width: 22px;
  background: white;
  border-radius: 9px;
  opacity: 1;
  transform: rotate(0deg);
  transition: .25s ease-in-out;
  left: 10px;
  &:nth-child(1){top: 6px;}
  &:nth-child(2){top: 13px;}
  &:nth-child(3){top: 20px;}
}
