/**
*
* Main Stylesheet For Visual Style Guide
* Mobile
*
**/

@import '_utils';
@import '_normalize';

// MDL Components
// Grid
@import '../../../vendor/mdl/material-design-lite-grid';
.mdl-grid {
  padding: 0;
}

// Hacer que todas las imagenes quepan en las grillas
img {
  max-width: 100%;
  /* Compatibilidad con IE11 y FF */
  width: 100%;
}

// Data-Table
@import '../../../vendor/mdl/data-table/_data-table';
// Extending table view
@import '_views/_table';


//FIELO
// Components

@import '_components/_list';
@import '_components/_catalog';
@import '_components/_banner';
@import '_components/_filters';
@import '_components/_wish';
@import '_components/_like';
@import '_components/_checkbox';
@import '_components/_spinner';
@import '_components/_addToCart';
@import '_components/_shoppingCart';
@import '_components/_orderBy';
@import '_components/_appMenu';


// FIELO
// Wrappers
.fielo-row {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}
@import '_outlines/_header';
@import '_outlines/_footer';
@import '_outlines/_modal';
@import '_outlines/_messages';
@import '_outlines/_content';

// Views
@import '_lists/_slider';
@import '_lists/_navigation';
@import '_lists/_pagination';
@import '_lists/_controls';
@import '_lists/_lists';
@import '_lists/_grid';

// Forms
@import '_forms/_forms';


// Make sure this is last to override anything else :)
@import '_helper';
