/**
*
* Slider
* Mobile
*
**/


.fielo-record-set--is-component-Catalog .fielo-slider {
   .fielo-field {
    float: right;
    width: 50%;
    & .fielo-field__label {
      font-weight: bold;
    }
  }

  .fielo-field--is-ViewImage {
    height: inherit;
    float: left;
    & .fielo-image {
      max-height: 100%;
    }
  }
}
