/**
*
* Shopping Cart
* Mobile
*
**/

.fielo-form__shopping-cart{
	.fielo-shopping__quantity {
		width: 40px;
    text-align: center;
	}
	.fielo-button__delete{
		cursor: pointer;
		&:hover{
			color:#85898C;
		}
	}
}

