/**
*
* Modal
* Mobile
*
**/


.fielo-modal {
  background-color: rgba(255,255,255,0.8);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height:100%;


  &--fade-in {
    animation: modal_fadein 2s;
  }

  @keyframes modal_fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
  }

  &--fade-out {
    animation: modal_fadeout 0.5s;
    animation-fill-mode:forwards;
  }
  @keyframes modal_fadeout {
    from { opacity: 1; }
    to   { opacity: 0; }
  }
}

.fielo-modal__body {
  background-color: #FFF;
  position: relative;
  top: 7%;
  width: 80%;
  height: 65%;
  border-radius: 5px;
  box-shadow: 0 5px 50px -4px #aaa;
  padding: 40px 20px 20px 20px;
  margin: auto;
}
.fielo-modal__body-container {
  overflow: auto;
  height: 100%;
}

.fielo-modal__close {
  cursor:pointer;
  position: absolute;
  text-align: right;
  right: 10%;
  top: 9%;
  z-index: 99999;
}
