/*
 * Reglas para dispositivos Tablet
 */
@import 'mobile';
// Breakpoints and stuff
@import 'tablet/_variables';

@import 'tablet/components/_components';


/** Larger Screens - desktops and tablets **/
@media #{$bp-tablet-1} {
  main {
    max-width: 864px;
    min-height: initial;
  }

  body {
    overflow-y: scroll;
  }
}
