/**
*
* Slider
* Mobile
*
**/

.fielo-banner .fielo-slider {
   .fielo-field {
    float: left;
    position: relative;
    width: 100%;
  }

  .fielo-field--is-ViewImage {
    position: absolute;
    top: 0;
    height: inherit;
    width: inherit;
    float: none;
    & .fielo-image {
      height: 100%;
      width: 100%;
    }
  }
}
