/**
*
* Messages
* Mobile
*
**/


.fielo-message {
	&--is-global{
		position: fixed;
		top: 0;
		width: 80%;
		margin-left: 10%;
		z-index: 99999999;
		text-align: center;
		border-bottom-right-radius: 5px;
		border-bottom-left-radius: 5px;
	  padding:3px 0 3px 0;
	}
	&--is-info {
	  color: #00529B;
	  background-color: #BDE5F8;
	}
	&--is-confirm {
	  color: #4F8A10;
	  background-color: #DFF2BF;
	}
	&--is-warning {
	  color: #9F6000;
	  background-color: #FEEFB3;
	}
	&--is-error,
	&--is-fatal {
	  color: #D8000C;
	  background-color: #FFBABA;
	}

  &--fade-in {
    animation: message_fadein 1s;
  }

  @keyframes message_fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
  }

  &--fade-out {
    animation: message_fadeout 0.5s;
    animation-fill-mode:forwards;
  }
  @keyframes message_fadeout {
    from { opacity: 1; }
    to   { opacity: 0; }
  }

  // boton con el contador
  &__counter {
    background-color: transparent;
    border: none;
    position: absolute;
    right: 3px;
    bottom: -2px;
  }
}


