/**
*
* Order By
* Mobile
*
**/
.fielo-record-set[data-layout="grid"]{
	.fielo-order-by{
		text-align: right;
	  right: 0;
	  font-size: 0.85em;
    position: relative;
	}
	.fielo-order-by__title{
	  display: inline-block;
	  float: none;
	  padding-top: 3px;
	}
	.fielo-order-by__list{
	  color: #000000;
	  padding: 3px 7px;
	  cursor: pointer;
	  border-radius: 5px;
	  font-size: inherit;
    float: none;
    display: inline-block;
    // &:hover .fielo-order-by__item{
    //   display: block;
    // }
	}

	.fielo-order-by__item{
		display:none;
		padding:0;
		&:hover{
			text-decoration: underline;
    }
  	&.active{
  		display:inline;
  	}
  }

	.fielo-order-by--is-open{
		.fielo-order-by__list{
 	 	  display:block;
		  box-shadow: 2px 3px 3px grey;
		  background-color: #FFFFFF;
		}
		.fielo-order-by__item{
			display:block;
      &.active{
			 text-decoration: underline;

      }
    }
		.fielo-order-by__title{
			display: none;
		}
	}
}


.fielo-record-set[data-layout="table"]{
	.fielo-order-by__item{
		cursor: pointer;
		&:hover{
			text-decoration: underline;
		}
    &.active{
  	  &.fielo-order-by__item--is-desc::after{
  		  content:'▼';
  	  }
  	  &.fielo-order-by__item--is-asc::after{
  		  content:'▲';
  	  }
    }
  }
}
