/**
*
* Slider
* Mobile
*
**/

$sliderHeight: 300px;
$arrowHeight: 30px;
$arrowWidth: 24px;

.fielo-slider {
  overflow: hidden;
  position: relative;

  // Oculta el paginador
  & + .fielo-paginator {
    display: none;
  }
  .fielo-field {
    position:relative;
    top:0;
    float: left;
    width: 100%;

  }
  .fielo-field--is-Image{
    position:absolute;
    width: inherit;
    height: inherit;
    .fielo-field__label {
      text-align: right;
      width: 100%;
      display: inline-block;
      position:absolute;
    }
    .fielo-image {
      height: 100%;
    }
  }
}

.fielo-slider__container {
  height: $sliderHeight;
  position: relative;

  &--is-sliding {
    animation: slide 1s;
    animation-fill-mode:forwards;
    animation-timing-function: ease-in;
  }

  @keyframes slide {
    to { left: 0; }
  }
}

.fielo-slider__slide {
  display: inline-block;
  cursor: pointer;
  height: inherit;
  &[data-link='']{
    cursor: default;
  }
}

// Flechas
.fielo-slider__arrow {
  $arrowColor: rgba(185, 185, 203,0.7);

  height: $sliderHeight;
  width: 6%;
  position: absolute;
  top: 0;
  cursor: pointer;

  &:hover{
    &:before{
      color: darken($arrowColor, 50%);
      border-color:darken($arrowColor, 50%);
    }
  }

  &:before {
    color: $arrowColor;
    width: $arrowWidth;
    display: block;
    border: 3px solid $arrowColor;
    border-radius: 100%;
    font-size: $arrowHeight;
    text-align: center;
    padding: 0 5px;
    line-height: $arrowHeight;
    font-weight: bold;
    top: 50%;
    margin-top: -1*($arrowHeight/2);
    position: relative;
  }
}

.fielo-slider__arrow--is-previous {
  &:before {
    content: "<";
    left: 5px;
  }
}

.fielo-slider__arrow--is-next {
  right:0;
  &:before {
    right: 5px;
    content: ">";
  }
}
