/**
*
* Menu
* Mobile
*
**/


.fielo-menu{
  &[data-app-menu = 'true']{display:none}
}

.fielo-menu__block {
  .fielo-list::before,
  .fielo-list::after {
    content: '';
    display: table;
  }
  .fielo-list::after {
    clear: both;
  }
  .fielo-list {
    padding: 10px;
  }
  .fielo-list__item{
    padding-left: 8px;
    padding-right: 8px;
    padding-top: 1px;
    padding-bottom: 1px;
    &.fielo-js-hover .fielo-list,
    &.fielo-js-hover .fielo-list__item {
      display: inherit;
    }
    a{
      display: block;
    }

    &.active{
      display: inherit;
    }
    .fielo-list {
      padding: 4px 8px 4px 8px;
    }
    .fielo-list__item {
      padding: 1px 5px 1px 5px;
    }
  }
}

.fielo-menu--isHorizontal .fielo-menu__block {
  .fielo-list__item{
    float: left;
    .fielo-list {
      position: absolute;
      top: 89%;
      left: 0%;
      float: left;
    }
  }
}
