/*
 * Reglas para dispositivos Mobile
 */
@import '../vendor/_h5bp';

// Breakpoints and stuff
@import 'mobile/_variables';

@import 'mobile/components/_components';

html {
  height: 100%;
}

html,
body {
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: 14px;
}

body {
  position: relative;
  font-weight: 300;
  box-sizing: border-box;
  min-height: 100%;
}

a {
  text-decoration: none;
  &:hover {
  text-decoration: underline;
  }
}
.base,
p,
ul,
ol {
  font-size: 14px;
}

main {
  margin: 0 auto;
  padding: 0;
  min-height: 100%;
}
