.fielo-menu[class*="--is-component-Filter"] {
	.fielo-list__item.active{
	  padding-right: 0;
      &::after{
        content: '✖';
        position: absolute;
        color: rgba(0, 0, 0, 0.3);
        right: 5px;
        font-size: 0.9em;
        line-height: 1.8em;
        top: 0;
        z-index: -1;
      }
    }
}
